@charset "UTF-8";
/* base */ /* media */
/*  */
/*  */
/*  box sizing */
@import "libs/swiper-bundle.min.css";
@import "libs/select2.min.css";
*,
*::before,
*::after {
  box-sizing: border-box;
}

/*    */
ul[class],
ol[class] {
  padding: 0;
}

/*    */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

/*    -  body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.35;
}

/*      ul  il,     class*/
ul[class],
ol[class] {
  list-style: none;
}

/*  a,    ,     */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/*     */
img {
  max-width: 100%;
  display: block;
}

/*        article*/
article > * + * {
  margin-top: 1em;
}

/*       */
input,
button,
textarea,
select {
  font: inherit;
}

/*       ,      */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/*  iOS   */
body {
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

@font-face {
  font-family: "SangBleuEmpire";
  src: url("../fonts/SangBleuEmpire-Regular.otf") format("opentype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SuisseBPIntl";
  src: url("../fonts/SuisseBPIntl-Regular.otf") format("opentype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
/* mixin */
/*    flex */
/*  */
/*    */
/* main */
body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100vh;
  font-family: "SuisseBPIntl", sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
}

.header--overlay + .sidebar + main > section:first-child {
  padding-top: 85px;
}
@media only screen and (max-width: 1279px) {
  .header--overlay + .sidebar + main > section:first-child {
    padding-top: 71px;
  }
}
@media only screen and (max-width: 767px) {
  .header--overlay + .sidebar + main > section:first-child {
    padding-top: 48px;
  }
}

@media only screen and (max-width: 1279px) {
  .header:not(.header--overlay) + .sidebar + main > *:first-child {
    padding-top: 71px;
  }
}
@media only screen and (max-width: 767px) {
  .header:not(.header--overlay) + .sidebar + main > *:first-child {
    padding-top: 48px;
  }
}

.header:not(.header--overlay) + .sidebar {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

main {
  flex-grow: 1;
  overflow: hidden;
  box-sizing: border-box;
  padding-left: 91px;
}
@media only screen and (max-width: 1279px) {
  main {
    padding-left: 0;
  }
}

a {
  color: #000000;
  text-decoration: none;
}

nav,
ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

input:focus-visible {
  outline: none;
}

.container {
  max-width: 1885px;
  width: 100%;
  padding: 0 35px;
  margin: 0 auto;
  box-sizing: border-box;
}
@media only screen and (max-width: 1279px) {
  .container {
    padding: 0 24px;
  }
}
@media only screen and (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}
.container--full {
  max-width: 100%;
}

.body-scroll-lock {
  overflow: hidden;
}

.d-none-mobile {
  display: block !important;
}
@media only screen and (max-width: 1279px) {
  .d-none-mobile {
    display: none !important;
  }
}

.d-none-desktop {
  display: none !important;
}
@media only screen and (max-width: 1279px) {
  .d-none-desktop {
    display: block !important;
  }
}

.d-none-mobile-flex {
  display: flex !important;
}
@media only screen and (max-width: 1279px) {
  .d-none-mobile-flex {
    display: none !important;
  }
}

.d-none-desktop-flex {
  display: none !important;
}
@media only screen and (max-width: 1279px) {
  .d-none-desktop-flex {
    display: flex !important;
  }
}

.link-area {
  position: absolute;
  inset: 0;
  z-index: 1;
}

.close-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 53px;
  height: 53px;
  border-radius: 50%;
  border: 1px solid rgba(28, 28, 28, 0.2);
  cursor: pointer;
}
@media only screen and (max-width: 767px) {
  .close-btn {
    width: 33px;
    height: 33px;
  }
}
@media (min-width: 1024px) {
  .close-btn:hover svg, .close-btn:focus svg {
    transform: rotate(180deg);
  }
}
.close-btn svg {
  stroke: #1c1c1c;
  width: 21px;
  height: 12px;
  transition: 0.3s ease;
}
@media only screen and (max-width: 767px) {
  .close-btn svg {
    width: 13px;
    height: 7px;
  }
}

.overlay {
  position: fixed;
  inset: 0;
  z-index: 30;
  background-color: rgba(1, 31, 24, 0.7);
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
}
.overlay.active {
  opacity: 1;
  pointer-events: auto;
}

/**
 *  Select 2 override
 */
.select2-dropdown {
  border: 0;
  border-radius: 0;
  top: 8px;
}

.select2-search--dropdown {
  display: none;
}

.select2-container--default .select2-results > .select2-results__options {
  padding: 15px;
  box-sizing: border-box;
}

.select2-results__option {
  font-family: "SuisseBPIntl", sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: #111111;
  text-transform: uppercase;
}
@media only screen and (max-width: 767px) {
  .select2-results__option {
    font-size: 9px;
    line-height: 13px;
  }
}

.select2-container--default .select2-results__option--selected {
  background-color: transparent;
}

.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
  background-color: transparent;
  color: #15a6b1;
}

.select2-container--default.select2-container--open .select2-selection--single .select2-selection__rendered::before {
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='12' height='8' viewBox='0 0 12 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M1.00049 7.18921L6.18968 2.00002L11.3789 7.18921' stroke='%2315A6B1' stroke-width='1.5'/%3e%3c/svg%3e ");
}

.select2-container--default .select2-selection--single {
  text-align: right;
  background-color: transparent;
  border: 0;
  border-radius: 0;
}
@media only screen and (max-width: 767px) {
  .select2-container--default .select2-selection--single {
    height: 15px;
  }
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  display: inline-block;
  padding-left: 20px;
  padding-right: 0;
  font-family: "SuisseBPIntl", sans-serif;
  font-size: 16px;
  line-height: 28px;
  text-transform: uppercase;
  color: #111111;
  position: relative;
}
@media only screen and (max-width: 767px) {
  .select2-container--default .select2-selection--single .select2-selection__rendered {
    font-size: 9px;
    line-height: 13px;
    padding-left: 13px;
  }
}
.select2-container--default .select2-selection--single .select2-selection__rendered::before {
  content: "";
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='12' height='8' viewBox='0 0 12 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M11.3784 1.18921L6.18923 6.3784L1.00004 1.18921' stroke='%2315A6B1' stroke-width='1.5'/%3e%3c/svg%3e ");
  background-repeat: no-repeat;
  background-size: 12px 8px;
  display: block;
  width: 12px;
  height: 8px;
  position: absolute;
  top: 50%;
  left: 0;
  right: unset;
  transform: translateY(-50%);
}
@media only screen and (max-width: 767px) {
  .select2-container--default .select2-selection--single .select2-selection__rendered::before {
    background-size: 8px;
  }
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
  display: none;
}

/**
 *  Swiper buttons
 */
.swiper-prev,
.swiper-next {
  background-color: rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.swiper-prev svg,
.swiper-next svg {
  fill: #ffffff;
}

.swiper-prev svg {
  transform: rotate(180deg);
}

.swiper-button-disabled {
  opacity: 0;
}

/* libs */
/* components */
.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-width: 65px;
  width: 100%;
  height: 65px;
  border: 0;
  border-radius: 50%;
  background-color: rgba(64, 64, 64, 0.1);
  transition: 0.3s ease;
}
@media only screen and (max-width: 1279px) {
  .button {
    max-width: 79px;
    height: 79px;
  }
}
@media only screen and (max-width: 767px) {
  .button {
    width: 33px;
    height: 33px;
  }
}
@media (min-width: 1024px) {
  .button:hover, .button:focus {
    background-color: rgba(64, 64, 64, 0.3);
  }
}
.button svg {
  width: 10px;
  height: 17px;
}
@media only screen and (max-width: 767px) {
  .button svg {
    width: 9px;
    height: 9px;
  }
}

.header {
  width: 100%;
  height: 85px;
  position: relative;
  z-index: 20;
  border-bottom: 1px solid rgba(64, 64, 64, 0.2);
  padding-left: 91px;
}
@media only screen and (max-width: 1279px) {
  .header {
    display: none;
    padding-left: 0;
  }
}
.header a {
  display: inline-flex;
  align-items: center;
  height: inherit;
  color: #404040;
  text-transform: uppercase;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: inherit;
}
.header__container > * {
  height: inherit;
}
.header__menu {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 96px;
}
@media only screen and (max-width: 1650px) {
  .header__menu {
    gap: 30px;
  }
}
.header__menu li {
  height: inherit;
}
.header__menu li a {
  text-decoration: underline;
  text-underline-offset: 5px;
  text-decoration-color: transparent;
  transition: 0.3s ease;
}
@media (min-width: 1024px) {
  .header__menu li a:hover, .header__menu li a:focus {
    text-decoration-color: inherit;
  }
}
.header__buttons {
  border-left: 1px solid rgba(64, 64, 64, 0.2);
  margin-right: -35px;
}
.header__button {
  transition: 0.3s ease;
}
@media (min-width: 1024px) {
  .header__button:hover, .header__button:focus {
    background-color: #15a6b1;
    color: #ffffff;
  }
}
.header__button--donate {
  padding: 0 72px;
}
.header__button--congrat {
  padding: 0 105px;
}
.header--overlay {
  position: absolute;
  top: 0;
  left: 0;
  border-bottom-color: rgba(255, 255, 255, 0.2);
}
.header--overlay a {
  color: #ffffff;
}
.header--overlay .header__buttons {
  border-left-color: rgba(255, 255, 255, 0.2);
}
@media (min-width: 1024px) {
  .header--overlay .header__button:hover, .header--overlay .header__button:focus {
    background-color: rgba(255, 255, 255, 0.2);
  }
}
.header--fixed {
  position: fixed;
  top: 0;
  left: 0;
  transition: transform 0.4s ease;
  background-color: #ffffff;
  border-bottom-color: rgba(64, 64, 64, 0.2);
}
.header--fixed a {
  color: #404040;
}
.header--fixed .header__buttons {
  border-left-color: rgba(64, 64, 64, 0.2);
}
@media (min-width: 1024px) {
  .header--fixed .header__button:hover, .header--fixed .header__button:focus {
    background-color: #15a6b1;
    color: #ffffff;
  }
}

.header-mobile {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 34;
  transform: translateX(-100%);
  transition: 0.3s ease;
  padding-top: 50px;
}
.header-mobile.open {
  transform: translateX(0);
}
.header-mobile__menu {
  text-align: center;
}
.header-mobile__menu li + li {
  margin-top: 15px;
}
.header-mobile__menu li a {
  text-decoration: underline;
  text-underline-offset: 5px;
  text-decoration-color: transparent;
  transition: 0.3s ease;
}
@media (min-width: 1024px) {
  .header-mobile__menu li a:hover, .header-mobile__menu li a:focus {
    text-decoration-color: inherit;
  }
}
.header-mobile__close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
}

.sidebar {
  width: 91px;
  height: 100vh;
  background-color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
}
@media only screen and (max-width: 1279px) {
  .sidebar {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 71px;
  }
}
@media only screen and (max-width: 767px) {
  .sidebar {
    height: 48px;
  }
}
.sidebar__logo, .sidebar__burger {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 85px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
@media only screen and (max-width: 1279px) {
  .sidebar__logo, .sidebar__burger {
    height: 100%;
    border-bottom: 0;
  }
}
@media only screen and (max-width: 1279px) {
  .sidebar__logo {
    width: 85px;
  }
}
@media only screen and (max-width: 767px) {
  .sidebar__logo {
    width: 60px;
  }
}
.sidebar__logo svg {
  width: 43px;
  height: 43px;
}
@media only screen and (max-width: 1279px) {
  .sidebar__logo svg {
    width: 35px;
    height: 35px;
  }
}
@media only screen and (max-width: 767px) {
  .sidebar__logo svg {
    width: 29px;
    height: 29px;
  }
}
.sidebar__burger {
  cursor: pointer;
}
@media only screen and (max-width: 1279px) {
  .sidebar__burger {
    width: 103px;
  }
}
@media only screen and (max-width: 1023px) {
  .sidebar__burger {
    width: 134px;
  }
}
@media only screen and (max-width: 767px) {
  .sidebar__burger {
    width: 70px;
  }
}
@media (min-width: 1024px) {
  .sidebar__burger:hover span::before, .sidebar__burger:focus span::before {
    transform: translateY(2px);
  }
  .sidebar__burger:hover span::after, .sidebar__burger:focus span::after {
    transform: translateY(-2px);
  }
}
.sidebar__burger span::before, .sidebar__burger span::after {
  content: "";
  display: block;
  width: 41px;
  height: 3px;
  background-color: #404040;
  transition: 0.3s ease;
}
@media only screen and (max-width: 1279px) {
  .sidebar__burger span::before, .sidebar__burger span::after {
    width: 55px;
  }
}
@media only screen and (max-width: 767px) {
  .sidebar__burger span::before, .sidebar__burger span::after {
    width: 36px;
  }
}
.sidebar__burger span::after {
  margin-top: 5px;
}

/* blocks */
.form {
  display: flex;
  flex-direction: column;
  max-width: 797px;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 31;
  background-color: #f9f9f9;
  overflow-y: auto;
  transform: translateX(100%);
  transition: 0.3s ease;
}
@media only screen and (max-width: 1279px) {
  .form {
    max-width: 100%;
    height: 82.3vh;
    bottom: 0;
    top: unset;
    transform: translateY(100%);
  }
}
@media only screen and (max-width: 767px) {
  .form {
    height: 80vh;
  }
}
.form.open {
  transform: translateX(0);
}
@media only screen and (max-width: 1279px) {
  .form.open {
    transform: translateY(0);
  }
}
.form__content {
  padding: 42px 50px 38px;
  flex-grow: 1;
}
@media only screen and (max-width: 1023px) {
  .form__content {
    padding: 25px 40px 38px 25px;
  }
}
@media only screen and (max-width: 767px) {
  .form__content {
    padding: 10px 15px;
  }
}
.form__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  margin-bottom: 43px;
}
@media only screen and (max-width: 767px) {
  .form__top {
    margin-bottom: 35px;
  }
}
.form__title {
  font-family: "SangBleuEmpire", sans-serif;
  font-size: 42px;
  line-height: 54px;
}
@media only screen and (max-width: 1023px) {
  .form__title {
    text-transform: uppercase;
  }
}
@media only screen and (max-width: 767px) {
  .form__title {
    font-size: 24px;
    line-height: 25px;
  }
}
.form input,
.form textarea {
  width: 100%;
  border: 0;
  padding: 20px 0;
  background-color: transparent;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  font-size: 16px;
  line-height: 20px;
  transition: 0.3s ease;
}
@media only screen and (max-width: 767px) {
