.form input::placeholder,
.form textarea::placeholder {
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: rgba(64, 64, 64, 0.3);
}
@media only screen and (max-width: 767px) {
  .form input::placeholder,
  .form textarea::placeholder {
    font-size: 9px;
    line-height: 13px;
  }
}
@media (min-width: 1024px) {
  .form input:hover, .form input:focus,
  .form textarea:hover,
