    margin-top: 14px;
  }
}
.form__files::-webkit-scrollbar {
  height: 6px;
  border-radius: 25px;
}
.form__files::-webkit-scrollbar-track {
  background-color: transparent;
}
.form__files::-webkit-scrollbar-thumb {
  background: rgba(64, 64, 64, 0.2);
}
.form__files-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 5px;
  color: #f9f9f9;
  text-transform: uppercase;
  background-color: #15a6b1;
  padding: 11px 14px;
  margin-right: 13px;
}
@media only screen and (max-width: 767px) {
  .form__files-item {
    font-size: 9px;
    line-height: 13px;
    padding: 7px;
  }
}
.form__files-item:first-child {
  width: 1px;
  height: 1px;
  visibility: hidden;
  pointer-events: none;
  padding: 0;
  margin: 0;
}
.form__files-item span {
  max-width: 120px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.form__files-item-remove {
  cursor: pointer;
}
.form__files-item-remove svg {
  stroke: #f9f9f9;
  width: 21px;
  height: 12px;
}
@media only screen and (max-width: 767px) {
  .form__files-item-remove svg {
    width: 13px;
    height: 7px;
  }
}
.form__download {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 255px;
  border: 1px dashed rgba(0, 0, 0, 0.3);
  margin-top: 10px;
  cursor: pointer;
  transition: 0.3s ease;
}
@media only screen and (max-width: 1023px) {
  .form__download {
    height: auto;
    border: none;
  }
}
@media (min-width: 1024px) {
  .form__download:hover, .form__download:focus {
    border-color: #000000;
  }
}
.form__download input {
